    height: 27em;
}
.image-gallery-thumbnail img {
    vertical-align: middle;
    width: 100%;
    height: 3.5em;
}
@media only screen and (max-width: 1068px) {
    .sampleimage-container {
        width: 80%;
    }
 }
 @media only screen and (max-width: 735px) {
    .sampleimage-container {
        width: 98%;
    }
 }
@-webkit-keyframes fadeIn {
    100% {
        opacity: 1;
    }
}

@keyframes fadeIn {
    100% {
        opacity: 1;
    }
}

.nav {
    opacity: 0;
    /* overflow: hidden; */
    z-index: 10;
    position: fixed;
    top: 0;
    width: 100%;
    -webkit-animation: fadeIn;
            animation: fadeIn;
    -webkit-animation-duration: 1s;
    animation-duration: 1s;
    -webkit-animation-iteration-count: 1;
            animation-iteration-count: 1;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
    -webkit-animation-delay: 1s;
            animation-delay: 1s;
}

.nav-icon {
    position: absolute;
    width: 50%;
    top: 0;
    left: 0;
    font-size: 1.1em;
    padding: 2em;
    color: white;
}

.nav-main {
    width: 50%;
    position: absolute;
    top: 0;
    right: 0;
    list-style-type: none;
    display: flex;
    justify-content: space-around;
}

.nav-link {
    display: inline-block;
    padding: 2em;
}

.nav-link a {
    font-size: 1.1em;
    color: white;
    text-decoration: none;
    text-transform: uppercase;
}

.nav-link a:hover {
    color: blue;
}
.nav-main-small {
    width: 100%;
    -webkit-transition: background 1s ease-in-out;
    transition: background 1s ease-in-out;
    display: none;
    position: absolute;
    top: 0;
    right: 0;
}
.nav-small-list {
    height: 0;
    width: 100%;
    text-align: center;
    font-size: 2em;
    font-weight: 150;
    -webkit-transition: height 1s ease-in-out;
    transition: height 1s ease-in-out;
    overflow: hidden;
}
.nav-small-button {
    display: block;
    margin: 0 auto;
    background: none;
    color: white;
    border: none;
    width: 100%;
    padding: 0.25em;
    font-size: 1.5em;
    font-weight: 200;
    border-bottom: 1px dotted white;
}
.nav-small-button:hover {
    cursor: pointer;
    color: blue;
}

.nav-small-link {
    /* height: 100%; */
    /* align-self: center; */
    color: white;
    margin: 1em 0;
    -webkit-transition: 0.5s all ease-in-out;
    transition: 0.5s all ease-in-out;
    /* border-bottom: 1px dotted white; */
}
.nav-small-link:hover {
    background-color: white;

}
.nav-small-link:hover a {
    color: black;
}
.nav-small-link a {
    /* align-self: center; */
    display: block;
    width: 100%;
    -webkit-transition: 0.5s all ease-in-out;
    transition: 0.5s all ease-in-out;
    color: white;
    text-decoration: none;
}

@media only screen and (max-width: 735px) {
    .nav-icon {
        display: none;
    }
    .nav-main {
        display: none;
    }
    .nav-main-small {
        display: inline;
        display: initial;
    }
 }
.App {
