.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #09d3ac;
}
.fade-enter {
  opacity: 0.5;
  z-index: 1;
}
.fade-enter.fade-enter-active {
  opacity: 1;
  transition: all 250ms ease-in-out;
}
